@import './common/reset.scss';
@import './common/fonts.scss';
@import './common/config.scss';
@import './common/variables.scss';
@import './common/general.scss';

.header {
  display: flex;
  justify-content: space-between;
  font-size: px-to-vw(16);
  padding: px-to-vw(60)  px-to-vw(40);
  position: relative;
  overflow: hidden;

  @include media-down(lg) {
    padding: px-to-vw(20)  px-to-vw(30);
  }

  input {
      display: block;
      width: 40px;
      height: 40px;     
      cursor: pointer;
      opacity: 0;
      z-index: 2;
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translate(0%, -50%);
  }

  .header__burger {
    display: none;

    @include media-down(lg) {
      display: block;
      width: 40px;
      height: 40px;
      position: relative;

      span {
        display: block;
        width: 40px;
        height: 4px;
        position: absolute;
        
        background: $c-text;
        border-radius: 3px;
        z-index: 1;
        transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    opacity 0.55s ease;

        &:first-child {
          top: 30%;
        }
                    
        &:nth-child(2) {
          top: 60%;
        }
      }
    }

    input:checked ~ .header__burger {
      span {
        opacity: 1;
        transform: rotate(45deg);
        background: #232323;
        top: 50%;
      }
    }

    input:checked ~ .header__burger {
      span:nth-last-child(2) {
        transform: rotate(-45deg);
      }
    }

    input:checked ~ .header__nav {
      transform: none;
    }
  }
}

.header__nav {
  display: flex;
  align-self: center;

  @include media-down(lg) {
    flex-direction: column;
    padding: 50px;
    
    background: $c-white;
    list-style-type: none;
    transform-origin: 0% 0%;
    transform: translate(-100%, 0);
    width: 100%;
    height: 100vh;
    z-index: 100;
    position: fixed;
    left: 0;
    top: 64px;
    
    transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
  }
}

.header__logo-wr {
  display: flex;
  align-items: center;
}

.header__nav-list {
  display: flex;
  list-style-type: none;
  align-items: center;

  &:first-child {
    margin-right: px-to-vw(310);
  }

  @include media-down(lg) {
    flex-direction: column;
    margin-right: 0;
    width: 100%;
  }
}

.header__nav-list__item {
  margin-right: px-to-vw(25);
  padding-right: px-to-vw(15);
  position: relative;

  a {
    cursor: pointer;
    color: inherit;
  }

  & > .header__nav-sublist::after {
    content: '\203a';
    position: absolute;
    right: 0;
    top: 0;
    transform:rotate(90deg);
    transition: transform 0.2s ease;
  }

  &:hover > .header__nav-sublist::after {
    transform:rotate(270deg);
  }

  &:last-child {
    margin-right: 0;
  }

  @include media-down(lg) {
    padding: 10px;
    font-size: px-to-vw(30);
    text-align: center;
  }
}

.header__nav-registration {
  display: flex;
  margin-left: px-to-vw(140);

  @include media-down(lg) {
    flex-direction: column;
    max-width: 40%;
    min-width: 30%;
    margin: 20px auto;
  }
}

.header__nav-registration {
  background-color: $c-gray;
  border-radius: 10px;
  padding: px-to-vw(6);
}

.header__nav-registration-btn {
  padding: px-to-vw(13) px-to-vw(25);
  font-weight: 500;
  color: $c-text;

  &--color {
    background-color: $c-primary;
    color: $c-white;
    border-radius: 10px;
  }
}

.header__nav-language {
  background-color: transparent;
  border: none;
  color: $c-gray-dark;
  font-size: px-to-vw(16);
}


@import './sections/header.scss';
@import './sections/hero.scss';

.cards__container {
  margin: auto;
  max-width: px-to-vw(1780);
  padding: px-to-vw(100) px-to-vw(10);
  position: relative;
  min-height: 100vh;
}

.cards__item-content {
  width: 50%;

  @include media-down(md) {
    width: 100%;
  }
}

.cards__item-picture {
  width: 47%;
  display: flex;
  justify-content: end;

  img {
    object-fit: contain;
  }

  @include media-down(md) {
    width: 100%;
  }
}

.cards__item-content__lable {
  display: block;
  color: $c-accent;
  margin-bottom: px-to-vw(80);
}

.cards__item-content__title {
  font-size: px-to-vw(50);
  margin-bottom: px-to-vw(40);
}

.cards__item-content__list {
  list-style-position: inside;
}

.cards__item-content__list-item {
  font-size: px-to-vw(28);
  opacity: 0.8;
  margin-bottom: px-to-vw(14);

  &:last-child {
    margin-bottom: 0;
  }
}

.cards__item  { 
	padding-top: 20px;
  margin-top: 50px;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  
  &:nth-child(1) {
    .cards__item-pagination__item {
      &:nth-child(1) {
        outline: 1px solid $c-accent;
        outline-offset: 3px;
      }
    }
  }

  &:nth-child(2) {
    .cards__item-pagination__item {
      &:nth-child(2) {
        outline: 1px solid $c-accent;
        outline-offset: 3px;
      }
    }
  }

  &:nth-child(3) {
    .cards__item-pagination__item {
      &:nth-child(3) {
        outline: 1px solid $c-accent;
        outline-offset: 3px;
      }
    }
  }

  @include media-down(lg) {
    position: static;
  }
}

.cards__item-body  { 
  position: relative;
  background-color: $c-text;
  border-radius: 30px;
  padding: px-to-vw(80);
  display: flex;
  justify-content: space-between;
  color: $c-white;
  max-height: calc(100vh - 130px);
  min-height: 570px;
  will-change: transform, opacity, box-shadow, border, z-index;
  transition:  0.5s ease-in-out;

  @include media-down(md) {
    max-height: 100%;
    padding: 42px 30px;
    margin-bottom: 30px;
    flex-direction: column;

    &:last-child {
      margin-bottom: 0px;
    }
  }


}

.cards__item-pagination {
  position: absolute;
  right: px-to-vw(35);
  top: 50%;
  transform: translate(-50%, -50%);

  @include media-down(md) {
    display: none;
  }
}

.cards__item-pagination__item {
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: $c-accent;
  margin-bottom: px-to-vw(22);

  &:last-child {
    margin-bottom: 0;
  }
}

.cards__label {
  width: px-to-vw(160);
  height: px-to-vw(160);
  background-color: $c-accent;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 5;
}

.cards__label-circle {
  display: block;
  overflow: visible;
  width: px-to-vw(110);
  font-size: px-to-vw(16);
  fill: $c-text;

  path {
    fill: none;
  }
}

.cards__label-arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
}

.hero__title-decoration-area {
  display: inline-block;
  overflow: hidden;

  span {
    display: inline-block;
  }
}

.cursor {
  --size: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  height:0;
  width:  0;
  background-color: #6D38F5BF;
  opacity: 0.9;
  filter: blur(0.3);
  border-radius: 50%;
  transform: translate3d(
    calc(100vw * var(--cursor-x) - var(--size) / 2), 
    calc(100vh * var(--cursor-y) - var(--size) / 2), 
    0);
  pointer-events: none;
  z-index: 10;
  transition: height 0.3s linear, width 0.3s linear;

  &.is-visible {
    height: var(--size);
    width:  var(--size);
  }

  @include media-down(md) {
    display: none;
  }
}