.hero__title {
    font-size: px-to-vw(80);
    font-weight: 500;
    text-align: center;
  }
  
  .hero__description {
    font-size: px-to-vw(28);
    font-weight: 400;
    color: $c-text;
    opacity: 0.7;
  }
  
  .hero__container {
    margin: auto;
    max-width: px-to-vw(1780);
    padding: px-to-vw(200) px-to-vw(10)  px-to-vw(100);
  }
  
  .hero__description {
    margin-top: px-to-vw(40);
    text-align: center;
  }
  
  .hero__title-decoration {
    position: relative;
    display: inline-block;
    width: px-to-vw(155);
    height: px-to-vw(80);
    border-radius: px-to-vw(60);
    background-color: $c-orange;
    vertical-align: baseline;
    overflow: hidden;
    font-size: px-to-vw(45);
  
    &--yellow {
      background-color: $c-accent;
    }
  }
  
  .hero__title-decoration__text {
    position: absolute;
    left: -100%;
    top: 50%;
    transform: translate(0, -50%);
    animation: ticker 3s linear infinite;
  }
  
  .hero__title-decoration__img {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, 100%);
  }
  
  .hero__description-hiding {
    overflow: hidden;
  }
  
  @keyframes ticker {
    0% {
      transform: translate(0, -50%);
    }
    100% {
      transform: translateX(-100%, -50%);
    }
  }